import styled from 'styled-components';

const StyledList = styled.div`
    display: block;
    margin: 15px;
    background-color: #fff;
    box-shadow: 0 8px 8px -8px #777777;
    a:hover {
        color: #fff;
        background-color: #41acc4;

    }
`;

export default StyledList;


